import { Injectable } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { HttpClient } from "@angular/common/http";
@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private fb:FormBuilder, private http:HttpClient) { 

  }

  readonly baseURI = 'http://develophase.gearhostpreview.com/api';

  formLogin = this.fb.group({
    UserName:['', Validators.required],
    Password:['', [Validators.required,Validators.minLength(4)]]
  });

  formRegistration = this.fb.group({
    UserName:['',Validators.required],
    Email:['',[Validators.required, Validators.email]],
    FullName:[''],
    Passwords : this.fb.group({
      Password:['',[Validators.required, Validators.minLength(4)]],
      ConfirmPassword:['',Validators.required]
    },{ validator: this.comparePasswords })

  });

  comparePasswords(fb:FormGroup){
    let confirmPasswordController = fb.get('ConfirmPassword');
    //passwordMismatch
    if(confirmPasswordController.errors == null || 'passwordMismatch' in confirmPasswordController.errors)
    {
      if(fb.get('Password').value!= confirmPasswordController.value)
      confirmPasswordController.setErrors({passwordMismatch: true});
      else
      confirmPasswordController.setErrors(null);
    }
  }

  register(){
    var body = {
      UserName: this.formRegistration.value.UserName,
      Email: this.formRegistration.value.Email,
      FullName: this.formRegistration.value.FullName,
      Password: this.formRegistration.value.Passwords.Password
    };
  return this.http.post(this.baseURI+'/ApplicationUser/Register', body);
  }

  login(){
    var body = {
      UserName: this.formLogin.value.UserName,
      Password: this.formLogin.value.Password
    };
  return this.http.post(this.baseURI+'/ApplicationUser/Login', body);
  }
}
